// Elevation Type

@mixin elevation-type {

  // Headings
  h1, h2, h3, h4, h5, h6,
  .h1, .h2, .h3, .h4, .h5, .h6 {
    margin-bottom: $headings-margin-bottom;
    font-family: $headings-font-family;
    font-weight: $headings-font-weight;
    line-height: $headings-line-height;
    color: $headings-color;
  }

  h1 { font-size: $font-size-h1; }
  h2 { font-size: $font-size-h2; }
  h3 { font-size: $font-size-h3; }
  h4 { font-size: $font-size-h4; }
  h5 { font-size: $font-size-h5; }
  h6 { font-size: $font-size-h6; }

  // These declarations are kept separate from and placed after
  // the previous tag-based declarations so that the classes beat the tags in
  // the CSS cascade, and thus <h1 class="h2"> will be styled like an h2.
  .h1 { font-size: $font-size-h1; }
  .h2 { font-size: $font-size-h2; }
  .h3 { font-size: $font-size-h3; }
  .h4 { font-size: $font-size-h4; }
  .h5 { font-size: $font-size-h5; }
  .h6 { font-size: $font-size-h6; }

  .lead {
    font-size: $lead-font-size;
    font-weight: $lead-font-weight;
  }

  // Type display classes
  .display-1 {
    font-size: $display1-size;
    font-weight: $display1-weight;
  }
  .display-2 {
    font-size: $display2-size;
    font-weight: $display2-weight;
  }
  .display-3 {
    font-size: $display3-size;
    font-weight: $display3-weight;
  }
  .display-4 {
    font-size: $display4-size;
    font-weight: $display4-weight;
  }


  //
  // Horizontal rules
  //

  hr {
    margin-top: $spacer-y;
    margin-bottom: $spacer-y;
    border: 0;
    border-top: $hr-border-width solid $hr-border-color;
  }


  //
  // Emphasis
  //

  small,
  .small {
    font-size: 80%;
    font-weight: normal;
  }

  mark,
  .mark {
    padding: .2em;
    background-color: $state-warning-bg;
  }


  //
  // Lists
  //

  .list-unstyled {
    @include list-unstyled;
  }

  // Inline turns list items into inline-block
  .list-inline {
    @include list-unstyled;
  }
  .list-inline-item {
    display: inline-block;

    &:not(:last-child) {
      margin-right: $list-inline-padding;
    }
  }

  //
  // Misc
  //

  // Builds on `abbr`
  .initialism {
    font-size: 90%;
    text-transform: uppercase;
  }

  // Blockquotes
  .blockquote {
    padding: ($spacer / 2) $spacer;
    margin-bottom: $spacer;
    font-size: $blockquote-font-size;
    border-left: .25rem solid $blockquote-border-color;
  }

  .blockquote-footer {
    display: block;
    font-size: 80%; // back to default font-size
    line-height: $line-height;
    color: $blockquote-small-color;

    &::before {
      content: "\2014 \00A0"; // em dash, nbsp
    }
  }

  // Opposite alignment of blockquote
  .blockquote-reverse {
    padding-right: $spacer;
    padding-left: 0;
    text-align: right;
    border-right: .25rem solid $blockquote-border-color;
    border-left: 0;
  }

  .blockquote-reverse .blockquote-footer {
    &::before {
      content: "";
    }
    &::after {
      content: "\00A0 \2014"; // nbsp, em dash
    }
  }
}
